import { AuxiliumComponent } from "../auxilium-component";

export class FrameContent extends AuxiliumComponent {
	static observable = ["spacing"];
	static observableBooleans = ["centred"];

	parsedCallback() {
		this.appendChild(document.createElementWithContents("main", this.childNodes, { className: this.centred ? "container" : "frame__main", id: "mainContent" }));
	}
}
