import { AuxiliumComponent } from "../auxilium-component";

export class Beacon extends AuxiliumComponent {
	static observable = ["size"];
	static observableBooleans = ["paused"];

	constructor() {
		super();

		this.variation ??= "theme";
	}

	parsedCallback() {
		this.classList.add("beacon", `beacon--${this.variation.toLowerCase()}`);

		if (this.size) { this.classList.add(`beacon--${this.size.toLowerCase()}`) };
		if (this.paused) { this.classList.add("beacon--off"); }
	}
}
