import { Browser } from "../core/browser"
import { Dialog } from "../dialog/dialog"

let UnloadInitialized = false

export class Ajax {
  static DefaultOptions = {
    method: "GET",
    headers: {},
    preventSlidingExpiration: false,
    processData: true,
    ResponseOverrides: {},
    LoadTimeout: 2000,
    error: error => Dialog.ShowError(Localizer.ERROR_TITLE, Localizer.ERROR_MESSAGE, error)
  }

  static async request(url, options = {}) {
    url = Browser.ResolveRelativeURL(url)
    const opts = { ...this.DefaultOptions, ...options }
    const { method, headers, body, queryParameters = {}, preventSlidingExpiration, LoadTimeout, processData, OnBeforeSend, OnCheckValid, OnComplete, success, error } = opts
    const abortController = new AbortController()
    let preventTimeout = false

    if (LoadTimeout) setTimeout(() => { if (!preventTimeout) Ajax.showThrobber() }, LoadTimeout)

    try {
      let payload = body
      const finalHeaders = {
        'RequestVerificationToken': document.querySelector("input[name='__RequestVerificationToken']")?.value,
        ...headers
      }
      if (preventSlidingExpiration) finalHeaders["x-prevent-sliding-expiration"] = true

      if (body instanceof HTMLFormElement) {
        payload = new FormData(body)
        payload.delete("__RequestVerificationToken")
      } else if (body && (["Object", "Number", "String"].includes(body.constructor.name) || Array.isArray(body))) {
        finalHeaders['Content-Type'] = 'application/json'
        payload = JSON.stringify(body)
      }

      const config = { method, headers: finalHeaders, body: payload, signal: abortController.signal }

      if (OnBeforeSend) await OnBeforeSend(config)

      const parsedURL = new URL(url, location)
      parsedURL.hash = ""
      Object.entries(queryParameters).forEach(([key, val]) => {
        Array.isArray(val) ? val.forEach(v => parsedURL.searchParams.append(key, v)) : parsedURL.searchParams.set(key, val)
      })

      if (OnCheckValid?.(config) === false) {
        Ajax.hideThrobber()
        preventTimeout = true
        OnComplete?.()
        return
      }

      const response = await fetch(parsedURL.toString(), config)
      const content = processData ? await response.text() : ""
      const result = {
        content,
        blob: () => response.blob(),
        json: () => response.json(),
        body: processData ? Browser.TryParseJSON(content) : response.body,
        url: response.url,
        headers: response.headers,
        ok: response.ok,
        redirected: response.redirected,
        status: response.status,
        statusText: response.statusText,
        type: response.type
      }

      preventTimeout = true
      OnComplete?.(result)
      Ajax.handleResponse(result, success)
      return result
    } catch (err) {
      Ajax.hideThrobber()
      preventTimeout = true
      if (!UnloadInitialized) error?.(err)
    }
  }

  static Get(url, options) { return this.request(url, { ...options, method: "GET" }) }
  static Post(url, options) { return this.request(url, { ...options, method: "POST" }) }
  static Put(url, options) { return this.request(url, { ...options, method: "PUT" }) }
  static Delete(url, options) { return this.request(url, { ...options, method: "DELETE" }) }
  static Patch(url, options) { return this.request(url, { ...options, method: "PATCH" }) }

  static handleResponse(response, overrides = {}) {
    Ajax.hideThrobber()
    overrides = { ...this.DefaultOptions.ResponseOverrides, ...overrides }
    overrides.any?.(response)
    const handler = response.ok ? overrides.ok : overrides[response.status]
    if (typeof handler === "function") return handler(response)

    switch (response.status) {
      case 400: console.log(response); return Dialog.ShowError(Localizer.ERROR_TITLE, Localizer.BAD_REQUEST)
      case 403: return Dialog.ShowError(Localizer.ACCESS_DENIED_TITLE, Localizer.ACCESS_DENIED)
      case 404: console.log(response); return Dialog.ShowError(Localizer.ERROR_TITLE, Localizer.NOT_FOUND)
      case 500: return Dialog.ShowError(Localizer.ERROR_TITLE, Localizer.ERROR_MESSAGE)
      default: if (response.ok && response.redirected) location.href = response.url
    }
  }

  static showThrobber() {
    document.body.classList.add("blur")
    document.body.appendChild(
      document.createElementWithContents(
        "div",
        "<div class='cubes'><div class='cube'></div><div class='cube'></div><div class='cube'></div><div class='cube'></div><div class='cube'></div></div>",
        { className: "ajax-throbber" }
      )
    )
  }

  static hideThrobber() {
    document.body.removeClass("blur")
    document.querySelector(".ajax-throbber")?.remove()
  }
}

window.addEventListener("beforeunload", () => UnloadInitialized = true)
